/*
  This boilerplate is based in ITCSS and SMACSS
*/
@import url("https://fonts.googleapis.com/css?family=Poppins");
@import url("https://fonts.googleapis.com/css?family=Open+Sans:700");
*,
*::before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  line-height: 1.6;
  font-family: "Poppins", sans-serif;
  background-color: #FCFCFC;
  color: #53575A;
  padding-top: 51px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Open Sans", sans-serif;
  margin: 0;
}

p {
  margin: 0;
}

img {
  max-width: 100%;
  width: 100%;
  display: block;
}

.header {
  text-align: center;
  border-bottom: 0.5px solid #DFE0E0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #1190C5;
  color: #FFF;
  text-shadow: 1px 0 2px #282D31;
  z-index: 10;
}

.heroes-container {
  text-align: center;
  padding: 0.5em;
}

@media screen and (min-width: 1024px) {
  .heroes-container {
    width: 1024px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: .5em;
    margin-top: 1em;
  }
}

.heroes-container .hero {
  position: relative;
  margin-bottom: 2em;
  overflow: hidden;
}

@media screen and (min-width: 1024px) {
  .heroes-container .hero {
    margin: 0;
  }
  .heroes-container .hero:hover .hero-description {
    bottom: 0;
  }
}

.heroes-container .hero-name {
  font-size: 1em;
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  transform: translate(-50%);
  background: rgba(40, 45, 49, 0.9);
  color: #FFF;
  border-bottom: 2px solid #1190C5;
}

@media screen and (min-width: 1024px) {
  .heroes-container .hero-image {
    width: 350px;
  }
}

.heroes-container .hero-description {
  position: absolute;
  bottom: -100%;
  left: 50%;
  width: 100%;
  transform: translate(-50%);
  background: rgba(40, 45, 49, 0.95);
  color: #FFF;
  transition: all .5s ease;
  font-size: 0.9em;
  font-family: "Open Sans", sans-serif;
  padding: 0.3em;
}

.lightbox {
  width: 100%;
  height: 100vh;
}

.lightbox-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.9);
  z-index: 20;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.lightbox-container {
  text-align: center;
  background: #FFF;
  padding: 0.2em;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  position: relative;
}

@media screen and (min-width: 1024px) {
  .lightbox-container {
    width: 33%;
    margin: 0 auto;
    height: 90%;
  }
}

.lightbox-description {
  color: #DD3F3B;
  font-weight: 600;
}

.lightbox-navigation__counter {
  font-size: .8em;
}

.next {
  position: absolute;
  top: 45%;
  right: -20px;
}

.next::after {
  content: url("https://image.flaticon.com/icons/svg/180/180000.svg");
  position: absolute;
  top: 0;
  right: 0;
  width: 35px;
  height: 35px;
  background: white;
  border-radius: 50%;
  z-index: 10;
  border: 1px solid #FFF;
}

.prev {
  position: absolute;
  top: 45%;
  left: -20px;
}

.prev::after {
  content: url("https://image.flaticon.com/icons/svg/179/179983.svg");
  position: absolute;
  top: 0;
  left: 0;
  width: 35px;
  height: 35px;
  z-index: 10;
  background: white;
  border-radius: 50%;
  border: 1px solid #FFF;
}

.close-modal {
  position: relative;
}

.close-modal::after {
  content: url("https://image.flaticon.com/icons/svg/148/148766.svg");
  position: absolute;
  top: -20px;
  right: -20px;
  width: 35px;
  height: 35px;
  z-index: 10;
  border-radius: 50%;
  border: 2px solid #282D31;
}

.search {
  margin: 1em auto;
  display: block;
  padding: 0.5em;
}
